.pageButton{
    position: relative;
    &.active{
        font-weight: 600;
    }
}
.selectedPage{
    font-weight: 700;
}

.pagination{
    display: flex; 
    align-items: center;
    justify-content: center;
    gap: 10px;
    &__pages{
        display: flex;
        align-items: center;
        gap: 8px;
    }
}

